import React from 'react';
import './CarItemDescription.scss';
import CarObject from '../../../../../../types/types';
import { Link } from 'react-router-dom';
interface IProps {
    obj: CarObject
}
const CarItemDescription: React.FC<IProps> = (props) => {
    return (
        <div className='CarItemDescription'>
            <div className='CarItem__MainDescription'>
                <p className="Car-Name">
                    <Link style={{textDecoration:"none"}} to={`/carpage/${props.obj._id}`}>
                        <span>{props.obj.title}</span>
                    </Link>

                </p>
                <p className="Car-Equipment">
                    <span>{props.obj.equipment}</span>
                </p>
                <p>
                    <span className='Description-Name'>:</span><span className='Description-Value'>{props.obj.rate}</span>
                </p>
                <p>
                    <span className='Description-Name'>:</span><span className='Description-Value'>{props.obj.year}</span>
                </p>
                <p>
                    <span className='Description-Name'>:</span><span className='Description-Value'>{props.obj.capacity}</span>
                </p>
                <p>
                    <span className='Description-Name'>:</span><span className='Description-Value'>{props.obj.transmission}</span>
                </p>
                <p>
                    <span className='Description-Name'>:</span><span className='Description-Value'>{props.obj.body}</span>
                </p>
                <p>
                    <span className='Description-Name'>:</span><span className='Description-Value'>{props.obj.mileage}</span>
                </p>
            </div>
            <div className='CarItem__SecondaryDescription'>
                <p>
                    <span className='Description-Name'>:</span><span className='Description-Value'>{props.obj.auction}</span>
                </p>
                <p>
                    <span className='Description-Name'>:</span><span className='Description-Value'>{props.obj.lot}</span>
                </p>
                <p>
                    <span className='Description-Name'>:</span><span className='Description-Value'>{props.obj.status}</span>
                </p>
                <p>
                    <span className='Description-Name'>:</span><span className='Description-Value'>{props.obj.date}</span>
                </p>
                <p>
                    <span className='Description-Name'> :</span><span className='Description-Value'>{props.obj.lastBet}</span>
                </p>
                <p>
                    <span className='Description-Name'>    :</span><span className='Description-Value'>{props.obj.price}</span>
                </p>
            </div>
        </div>

    )

}
export default CarItemDescription;