import React from "react";
import './ListSort.scss'
import {Select} from '@gravity-ui/uikit';
interface IProps {
    setSort: React.Dispatch<React.SetStateAction<string>>
}
const ListSort: React.FC<IProps> = ({ setSort }) => {
    return (
        <div className="ListSort">
            <Select 
                size="m"
                placeholder=":"
                className="Sort-Select" 
                popupClassName="Sort-Select__option"
                onUpdate={(str) =>{setSort(str[0])}}>
                <Select.Option value="priceup">&nbsp;&nbsp;,  </Select.Option>
                <Select.Option value="pricedown">&nbsp;&nbsp;,  </Select.Option>
                <Select.Option value="" disabled={true}>-------------------------------------------</Select.Option>
                <Select.Option value="betup">&nbsp;&nbsp; ,  </Select.Option>
                <Select.Option value="betdown">&nbsp;&nbsp; ,  </Select.Option>
                <Select.Option value="" disabled={true}>-------------------------------------------</Select.Option>
                <Select.Option value="dateup">&nbsp;&nbsp; ,  </Select.Option>
                <Select.Option value="datedown">&nbsp;&nbsp; ,  </Select.Option>
            </Select>
        </div>
    )
}
export default ListSort;